
"use client";

import React, { useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { attendanceRecords as initialAttendance, getEmployeeName } from '@/lib/data';
import type { Attendance } from '@/lib/types';
import { useUser } from '@/contexts/user-provider';
import { format } from 'date-fns';
import { Badge } from '@/components/ui/badge';

type Status = Attendance['status'];

export default function AttendancePage() {
  const { user } = useUser();
  const [attendance, setAttendance] = useState<Attendance[]>(initialAttendance);
  
  if(!user) return null;

  const canEdit = user.role === 'Admin' || user.role === 'Manager' || user.role === 'HR Staff';

  const handleStatusChange = (id: string, status: Status) => {
    setAttendance(attendance.map(att => att.id === id ? { ...att, status } : att));
  };

  const getStatusBadgeVariant = (status: Status) => {
    switch (status) {
      case 'Present':
        return 'default';
      case 'On Leave':
        return 'secondary';
      case 'Absent':
        return 'destructive';
    }
  };

  return (
    <div className="space-y-8">
      <h1 className="text-3xl font-bold">Attendance Records</h1>
      <Card>
        <CardHeader>
          <CardTitle>Daily Log</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Employee</TableHead>
                <TableHead>Date</TableHead>
                <TableHead>Status</TableHead>
                {canEdit && <TableHead className="text-right">Edit Status</TableHead>}
              </TableRow>
            </TableHeader>
            <TableBody>
              {attendance.map((record) => (
                <TableRow key={record.id}>
                  <TableCell>{getEmployeeName(record.employeeId)}</TableCell>
                  <TableCell>{format(record.date, "PPP")}</TableCell>
                  <TableCell>
                    <Badge variant={getStatusBadgeVariant(record.status)}>{record.status}</Badge>
                  </TableCell>
                  {canEdit && (
                    <TableCell className="text-right">
                      <Select
                        defaultValue={record.status}
                        onValueChange={(value: Status) => handleStatusChange(record.id, value)}
                      >
                        <SelectTrigger className="w-[180px]">
                          <SelectValue placeholder="Set status" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Present">Present</SelectItem>
                          <SelectItem value="Absent">Absent</SelectItem>
                          <SelectItem value="On Leave">On Leave</SelectItem>
                        </SelectContent>
                      </Select>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
